/**
  This is the entrypoint less file that's loaded onto index.html
*/
/* stylelint-disable */
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html {
  background: pink;
  box-sizing: border-box;
  font-family: 'Courier New', Courier, monospace;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.main_container {
  padding: 10rem 1rem;
}
@media all and (max-width: 360px) {
  .main_container {
    padding: 5rem 1rem;
  }
}
.content_card {
  width: 20rem;
  min-height: 36rem;
  background: white;
  border-radius: 1rem;
  box-shadow: 0px 1px 2px 1px #DDDDDD;
  margin: 0 auto;
  padding: 2rem;
}
@media all and (max-width: 360px) {
  .content_card {
    width: 90%;
    min-width: 16rem;
  }
}
.content h1 {
  font-size: 2rem;
}
.menu {
  display: flex;
}
.menu li a,
.menu li button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
  /* Remove excess padding and border in Firefox 4+ */
  /* my own styles */
  cursor: pointer;
  text-decoration: none;
}
.menu li a::-moz-focus-inner,
.menu li button::-moz-focus-inner {
  border: 0;
  padding: 0;
}
